.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 18px;
  font-weight: 500;
  color: rgba(72, 157, 242, 1);
  margin: 50px auto;
}

.thumb {
  margin: 0 auto;
  width: 300px;
}

.title {
  color: rgb(8, 28, 49);
}

.image {
  max-width: 100%;
  margin-bottom: 15px;
  border-radius: 20px;
}

.button {
  width: 150px;
  height: 40px;
  color: rgb(30, 131, 233);
  border-radius: 5px;
  padding: 10px 25px;
  font-size: 18px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
  background-color: #86baf7;
  background-image: linear-gradient(315deg, #86baf7 0%, #b8d6f9 74%);
  line-height: 42px;
  padding: 0;
  border: none;
  text-decoration: none;
}

.button:hover,
.button:focus {
  background-color: #b8d6f9;
  background-image: linear-gradient(315deg, #b8d6f9 0%, #86baf7 74%);
}

@media screen and (max-width: 500px) {
  .container {
    margin: 10px auto;
  }
}